  margin-left: 0;
}

.tui-pagination .tui-first-child, .tui-pagination .tui-prev-is-ellip {
  border-left: 1px solid #ddd;
}

.tui-pagination .tui-last-child, .tui-pagination .tui-next-is-ellip {
  border-right: 1px solid #ddd;
}

.tui-pagination .tui-first-child.tui-is-selected {
  border-left: 1px solid #4b96e6;
}

.tui-pagination .tui-last-child.tui-is-selected {
  border-right: 1px solid #4b96e6;
}

.tui-pagination .tui-first, .tui-pagination .tui-last, .tui-pagination .tui-next, .tui-pagination .tui-next-is-ellip, .tui-pagination .tui-prev, .tui-pagination .tui-prev-is-ellip {
  height: 26px;
  white-space: nowrap;
  border: 1px solid #ddd;
  padding: 0;
  font-size: 0;
  font-weight: 400;
  line-height: 26px;
  overflow: hidden;
}

.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}

.tui-pagination .tui-first + .tui-prev, .tui-pagination .tui-next + .tui-last, .tui-pagination .tui-next-is-ellip {
