}

.tui-pagination .tui-ico-ellip, .tui-pagination .tui-ico-first, .tui-pagination .tui-ico-last, .tui-pagination .tui-ico-next, .tui-pagination .tui-ico-prev {
  vertical-align: middle;
  background: url("data:image/png;base64,") no-repeat;
  font-size: 0;
  display: inline-block;
}

.tui-pagination .tui-next-is-ellip, .tui-pagination .tui-prev-is-ellip {
