.pop-up__backdrop {
  z-index: 100;
  min-width: 320px;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: initial;
  background-color: rgba(17, 17, 17, .4);
  padding: 20px;
  transition-property: opacity, visibility;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.pop-up__backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.pop-up__backdrop.is-hidden .modal__pop-up {
  opacity: 0;
  visibility: hidden;
  transform-origin: center;
  transform: translate(-50%, -50%)scale(.3);
}

.pop-up__book-info {
  max-width: 287px;
}

@media screen and (min-width: 768px) {
  .pop-up__book-info {
    width: 279px;
  }
}

.pop-up__block-img {
  max-width: 287px;
  border-radius: 8px;
  outline: 1px solid #b4afaf;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .pop-up__block-img {
    width: 192px;
    height: 281px;
  }
}

@media screen and (max-width: 767px) {
  .pop-up__block-img {
    margin-bottom: 14px;
  }
}

.modal__pop-up {
  min-width: 280px;
  max-width: 335px;
  max-height: 80vh;
  border: 2px solid var(--primary-text-color);
  background-color: var(--pop-up-bg-color);
  opacity: 1;
  visibility: initial;
  transform-origin: center;
  border-radius: 18px;
  padding: 40px 24px;
  transition-property: transform, opacity, visibility;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

@media screen and (max-width: 375px) {
  .modal__pop-up {
    width: auto;
    max-width: 335px;
  }
}

@media screen and (min-width: 768px) {
  .modal__pop-up {
    max-width: 579px;
    padding: 40px;
  }
}

.pop-up__button__close {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  padding: 0;
